export function GettimeifferenceOfNow(lastDate) {
    var lastDate = lastDate.replace(/\-/g, "/"); //replacedata.replace(/\-/g, "/")-/
    var date = new Date(lastDate);
    var date_last = date.getTime();
    var date_now = new Date().getTime();
    var day = Math.round((date_now - date_last) / (1000 * 60 * 60 * 24)); //
    var hours = Math.round((date_now - date_last) / (1000 * 60 * 60)); //
    var minutes = Math.round((date_now - date_last) / (1000 * 60)); //
    var myYear = date.getFullYear(); //(4,1970-????)
    var myMonth = date.getMonth() + 1; //(0-11,01)
    var myToday = date.getDate(); //(1-31)
    // var myDay = date.getDay(); //X(0-6,0)
    var myHour = date.getHours(); //(0-23)
    var myMinute = date.getMinutes(); //(0-59)
    // var mySecond = date.getSeconds(); //(0-59)
    // var week = ['', '', '', '', '', '', ''];
    if (day > 0) {
        if (day > 10) {
            return {
                DistanceNow: myYear + '' + myMonth + '' + myToday + '',
                Detailed: myMonth + '-' + myToday,
                MinuteDifference: minutes,
                HourDifference: hours,
            };
        }
        return {
            DistanceNow: `${day}`,
            Detailed: myMonth + '-' + myToday,
            MinuteDifference: minutes,
            HourDifference: hours,
        };
    }
    else if (hours > 0) {
        return {
            DistanceNow: `${hours}`,
            Detailed: (myHour < 10 ? '0' + myHour : myHour) + ':' + (myMinute < 10 ? '0' + myMinute : myMinute),
            MinuteDifference: minutes,
            HourDifference: hours,
        };
    }
    else if (minutes > 0) {
        return {
            DistanceNow: `${minutes}`,
            Detailed: (myHour < 10 ? '0' + myHour : myHour) + ':' + (myMinute < 10 ? '0' + myMinute : myMinute),
            MinuteDifference: minutes,
            HourDifference: hours,
        };
    }
    else {
        return {
            DistanceNow: ``,
            Detailed: (myHour < 10 ? '0' + myHour : myHour) + ':' + (myMinute < 10 ? '0' + myMinute : myMinute),
            HourDifference: hours,
            MinuteDifference: minutes
        };
    }
}
;
//
/**
 * @function -
 * @returns {{
 * MonthAndDay
 * YearAndMothAndDay:
 * weekday:
 * time: 
 * }}
 */
export function GetNowDate() {
    var date = new Date();
    // var date_now = new Date().getTime();
    var myYear = date.getFullYear(); //(4,1970-????)
    var myMonth = date.getMonth() + 1; //(0-11,01)
    var myToday = date.getDate(); //(1-31)
    var myDay = date.getDay(); //X(0-6,0)
    var myHour = date.getHours(); //(0-23)
    var myMinute = date.getMinutes(); //(0-59)
    // var mySecond = date.getSeconds(); //(0-59)
    var week = ['', '', '', '', '', '', ''];
    return {
        MonthAndDay: myMonth + '' + myToday + '',
        YearAndMothAndDay: myYear + '' + myMonth + '' + myToday + '',
        weekday: week[myDay],
        time: (myHour < 10 ? '0' + myHour : myHour) + ':' + (myMinute < 10 ? '0' + myMinute : myMinute)
    };
}
;
//
export function GetNumberOfMenit(date1, date2) {
    //date1date2
    date1 = date1.replace(/\-/g, "/"); //replacedata.replace(/\-/g, "/")-/
    date2 = date2.replace(/\-/g, "/");
    var date1_time = new Date(date1).getTime();
    var date2_time = new Date(date2).getTime();
    var menit = Math.round((date2_time - date1_time) / (1000 * 60)); //
    return menit;
}
;
//
export function getTime() {
    var myDate = new Date();
    var myYear = myDate.getFullYear(); //(4,1970-????)
    var myMonth = myDate.getMonth() + 1; //(0-11,01)
    var myToday = myDate.getDate(); //(1-31)
    // var myDay = myDate.getDay(); //X(0-6,0)
    var myHour = myDate.getHours(); //(0-23)
    var myMinute = myDate.getMinutes(); //(0-59)
    var mySecond = myDate.getSeconds(); //(0-59)
    // var week = ['', '', '', '', '', '', ''];
    var nowTime;
    nowTime = myYear + '/' + myMonth + '/' + myToday + ' ' + (myHour < 10 ? '0' + myHour : myHour) + ':' +
        (myMinute < 10 ? '0' + myMinute : myMinute) + ':' + (mySecond < 10 ? '0' + mySecond : mySecond);
    //console.log(nowTime);
    return nowTime;
}
;
