/**
   *
   * @description 
   * @param userId {id}
   * @returns {UserInfo}
   */
export async function getUserinfo(userId) {
    let res = await request({
        data: {
            method: 'GET',
            group: 'user',
            action: `${userId}/info`,
            data: {
                userId: userId,
            },
            header: {
                'content-type': 'application/x-www-form-urlencoded',
            },
        }
    });
    if (res.data.code === REQUEST_SUCCEEDED_CODE) {
        console.log(res.data.data);
        return res.data.data;
    }
    else {
        return;
    }
}
