/**
 * @function 
 * @param userId Id
 */
export async function deleteMessageRecord(userId) {
    InteractiveTypeList.forEach((item) => {
        let INTERACTIVE_MESSAGE_LIST = getInteractiveMessageListKey(userId, item);
        uni.setStorage({ key: INTERACTIVE_MESSAGE_LIST, data: null }); //
    });
    let USER_MESSAGE_LIST = getUserMessageListKey(userId);
    let userMessageList = await getUserMessageList(userId);
    userMessageList.forEach((item) => {
        let CHAT_RECORD = getChatRecordKey(userId, item.userId);
        uni.setStorage({ key: CHAT_RECORD, data: null }); //
    });
    uni.setStorage({ key: USER_MESSAGE_LIST, data: null }); //
}
export function getTime() {
    var myDate = new Date();
    var myYear = myDate.getFullYear(); //(4,1970-????)
    var myMonth = myDate.getMonth() + 1; //(0-11,01)
    var myToday = myDate.getDate(); //(1-31)
    // var myDay = myDate.getDay(); //X(0-6,0)
    var myHour = myDate.getHours(); //(0-23)
    var myMinute = myDate.getMinutes(); //(0-59)
    var mySecond = myDate.getSeconds(); //(0-59)
    // var week = ['', '', '', '', '', '', ''];
    var nowTime;
    nowTime = myYear + '-' + myMonth + '-' + myToday + ' ' + (myHour < 10 ? '0' + myHour : myHour) + ':' +
        (myMinute < 10 ? '0' + myMinute : myMinute) + ':' + (mySecond < 10 ? '0' + mySecond : mySecond);
    //console.log(nowTime);
    return nowTime;
}
